<template>
    <div class="publish-home">
        <div class="publish-c">
            <p class="publish-title">{{$t('french.publishHome[0]')}}</p>
            <p class="publish-desc">{{$t('french.publishHome[1]')}}</p>
            <div class="btn-group">
                <span class="btn-dell publish-btn" @click="sell">
                    <img src="../../../assets/img/frenchImg/btn_sell.png" alt="" class="btn-img">
                    <span class="sell btn-name">{{$t('french.publishHome[2]')}}</span>
                </span>
                <span class="btn-buy publish-btn" @click="buy">
                    <span class="buy btn-name">{{$t('french.publishHome[3]')}}</span>
                    <img src="../../../assets/img/frenchImg/btn_buy.png" alt="" class="btn-img">
                </span>
            </div>
            <p class="join-btn">
                <span class="change-btn" @click="becomeMerchant" v-if="isShop == 0">{{$t('french.publishHome[4]')}}</span>
                <span class="change-btn" v-if="isShop == 2">{{$t('french.publishHome[5]')}}...</span>
                <span class="change-btn" v-if="isShop == 1" @click="cancelStore">{{$t('french.publishHome[6]')}}</span>
            </p>
            <p class="publish-detail">{{$t('french.publishHome[7]')}}</p>
        </div>
        <el-dialog
        :title="$t('french.publishHome[8]')"
        :visible.sync="dialogVisible">
            <p class="step">{{$t('french.publishHome[9]')}}</p>
            <p class="introduce">{{$t('french.publishHome[10]')}}</p>
            <p class="step">{{$t('french.publishHome[11]')}}</p>
            <p class="introduce">{{$t('french.publishHome[12]')}}</p>
            <template>
                <!-- `checked` 为 true 或 false -->
                <el-checkbox v-model="checked">{{$t('french.publishHome[13]')}}</el-checkbox>
            </template>
            <div class="btn-team clr">
                <el-button type="primary" size="mini" class="dialog-btn" @click="join">{{$t('french.publishHome[14]')}}</el-button>
                <el-button size="mini" class="dialog-btn cancel-btn" @click="cancel">{{$t('french.publishHome[15]')}}</el-button>
            </div>
        </el-dialog>
        <el-dialog
        :title="$t('french.publishHome[23]')"
        :visible.sync="cancelDialogVisible">
            <p>{{ $t('french.publishHome[24]') }}</p>
            <div class="btn-team clr">
                <el-button type="primary" size="mini" class="dialog-btn" @click="submitDelStore">{{$t('french.publishHome[14]')}}</el-button>
                <el-button size="mini" class="dialog-btn cancel-btn" @click="cancel">{{$t('french.publishHome[15]')}}</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import { addShop, delShop } from "../../../api/c2c.js"
import { userInfo } from "../../../api/public.js"
export default {
    name: 'publishHome',
    data() {
        return {
            checked: true,
            cancelDialogVisible: false,
            dialogVisible: false,
            isShop: 0
        }
    },
    created() {
        if (!sessionStorage.account || !sessionStorage.token) {
            this.isShop = 0
        } else {
            this.isShop = JSON.parse(sessionStorage.getItem('userInfo')).is_shop
            this.getUserInfo()
        }
    },
    methods: {
        // 获取个人信息
        getUserInfo() {
            userInfo().then((r)=>{
                if(r.data.status ==200){
                    this.isShop = r.data.data.is_shop
                    sessionStorage.userInfo = JSON.stringify(r.data.data)   
                }
            })
        },
        // 申请成为商家
        becomeMerchant() {
            if (!sessionStorage.account || !sessionStorage.token) {
                this.$public.msg(this.$t('french.publishHome[16]'), 'error' ,this)
                this.$router.push({ name: 'login' })
                return
            } else {
                this.dialogVisible = true
                return
            }
        },
        // 提交申请
        join() {
            if (this.checked == true) {                                                                                                                                                                                     
                addShop({ account: sessionStorage.getItem('account') }).then(res=> {
                    if (res.data.status == 200) {
                        this.$public.msg(this.$t('french.publishHome[17]'), 'success', this)
                        this.dialogVisible = false
                        this.isShop = 2
                        return
                    } else {
                        this.$public.msg(res.data.msg, 'error', this)
                        this.dialogVisible = false
                        return
                    }
                })
            } else {
                this.$public.msg(this.$t('french.publishHome[18]'), 'error', this)
                return
            }
        },
        // 取消商家资质
        cancelStore() {
            this.cancelDialogVisible = true
        },
        // 确认撤销商家认证
        submitDelStore() {
            delShop({ account: sessionStorage.getItem('account') }).then(res=> {
                if (res.data.status == 200) {
                    this.$public.msg(this.$t('french.publishHome[25]'), 'success', this)
                    this.cancelDialogVisible = false
                    this.isShop = 0
                    return
                } else {
                    this.$public.msg(res.data.msg, 'error', this)
                    this.cancelDialogVisible = false
                    return
                }
            })
        },
        // 出售
        sell() {
            if (!sessionStorage.account || !sessionStorage.token) {
                this.$public.msg(this.$t('french.publishHome[19]'), 'error' ,this)
                this.$router.push({ name: 'login' })
            } else if (this.isShop != 1) {
                this.$public.msg(this.$t('french.publishHome[20]'), 'error' ,this)
                return
            } else {
                this.$router.push({ name: 'releaseSell' })
            }   
        },
        // 求购
        buy() {
            if (!sessionStorage.account || !sessionStorage.token) {
                this.$public.msg(this.$t('french.publishHome[21]'), 'error' ,this)
                this.$router.push({ name: 'login' })
            } else if (this.isShop != 1) {
                this.$public.msg(this.$t('french.publishHome[22]'), 'error' ,this)
                return
            } else {
                this.$router.push({ name: 'releaseBuy' })
            }   
        },
        // 关闭弹窗
        cancel() {
            this.dialogVisible = false
            this.cancelDialogVisible = false
        }
    }
}
</script>

<style lang=less>
    @import "./publishHome.less";
    @import "../../../assets/index.less";
</style>



